export default "@import '../../themes/src/default.css';\n@import '../../backdrop/src/vars.css';\n\n:root {\n    --drawer-width: 500px;\n}\n\n.component {\n    position: fixed;\n    top: 0;\n    height: 100%;\n    width: var(--drawer-width);\n    flex: 1;\n    overflow: auto;\n    will-change: transform;\n}\n\n.rightPlacement {\n    right: 0;\n    align-self: flex-end;\n}\n\n.leftPlacement {\n    left: 0;\n    align-self: flex-start;\n}\n\n.content {\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    flex: 1;\n}\n\n.simplebar {\n    height: 100vh;\n}\n\n/* enter */\n\n.enterRight {\n    transform: translateX(100%);\n}\n\n.enterLeft {\n    transform: translateX(-100%);\n}\n\n.contentEnter {\n    opacity: 0;\n}\n\n.backdropEnter {\n    background-color: var(--backdrop-hidden-background);\n}\n\n.enterActive {\n    transition: transform 0.3s ease-in-out;\n    transform: translateX(0);\n}\n\n.backdropEnterActive,\n.backdropEnterDone {\n    transition: background 0.3s ease-in-out;\n    background-color: var(--backdrop-visible-background);\n}\n\n.contentEnterActive {\n    transition: opacity 0.2s ease-in-out 0.3s;\n    opacity: 1;\n}\n\n/* exit */\n\n.exit {\n    transform: translateX(0);\n}\n\n.backdropExit {\n    background-color: var(--backdrop-visible-background);\n}\n\n.contentExit {\n    opacity: 1;\n}\n\n.exitActiveRight {\n    transition: transform 0.25s ease-in-out 0.1s;\n    transform: translateX(100%);\n}\n\n.exitActiveLeft {\n    transition: transform 0.25s ease-in-out 0.1s;\n    transform: translateX(-100%);\n}\n\n.backdropExitActive,\n.backdropExitDone {\n    transition: background 0.25s ease-in-out 0.1s;\n    background-color: var(--backdrop-hidden-background);\n}\n\n.contentExitActive {\n    opacity: 0;\n    transition: opacity 0.12s ease-in-out;\n}\n";